<template>
  <div class="waterLevel">
    <!-- 水位效正 -->
    <p class="waterLevelTitle">
      水位效正
      <span style="float: right">
        <vxe-button
          status="primary"
          content="返回"
          @click="$emit('initFocus')"
        ></vxe-button>
      </span>
    </p>
    <div class="waterLevelBody" :style="{height:height - 90+'px'}">
      <div class="waterLevelList">
          <p  v-for="(item,index) of list" :key="index" @click="itemClick(index)" :class="{waterLeveItem:true,actionWaterLeve:item.pipe==selectRow.pipe}">
            {{ item.codeName }}  {{setPipe(item.pipe)}}
          </p>
      </div>
      <div class="waterLevelCenter">
        <p style="height:35px;">   {{ selectRow.codeName||"" }}  {{setPipe(selectRow.pipe)||""}} </p>
        <p class="centerTitle"> 
          <span class="span">AD</span>
          <span class="span">水高</span>
        </p>
        <div class="multiColumn" style="margin-bottom: 10px;">
          <div>低端</div>
          <div><input type="text" v-model="selectRow.lAD" /></div>    
          <div  class="multiColumn3"><input type="text" v-model="selectRow.lADA" /></div>    
        </div>  
        <div class="multiColumn"  style="margin-bottom: 10px;">
          <div>高端</div>
          <div><input type="text" v-model="selectRow.hAD" /></div>    
          <div  class="multiColumn3"><input type="text" v-model="selectRow.hADA" /></div>    
        </div>  
        <div class="multiColumn"  style="margin-bottom: 10px;">
          <div>当前值</div>
          <div style="font-size:16px;color:#333">{{selectRow.AD}}</div>    
          <div class="multiColumn3" style="font-size:16px;color:#333">{{selectRow.VAD}}</div>      
        </div>  
        <p style="text-align:end">
         <vxe-checkbox v-model="selectRow.enableAlert" content="启用水位报警"></vxe-checkbox>  &nbsp;&nbsp;
          <vxe-button
          status="primary"
          content="校正"   
          :disabled="selectRow.pipe==0"
          @click="submit"
        ></vxe-button>
        </p>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  data() {
    return {
      height: document.documentElement.clientHeight || document.body.clientHeight,
      communication: {},
      selectRow:{
        code :"", // 助剂编号
        codeName  :"", // 助剂名称
        pipe  :0, // 管道号
        address  :"", // 称地址
        flow  :"", //流量系数
        AD  :"",//模拟输入值
        VAD  :"",  //显示水高
        lAD  :"", //低端AD
        lADA :"", //低端水高
        hAD  :"",  //高端AD
        hADA :"", //高端水高
        PAD  :"",  //百分比数据
        ConversionRate  :"",//变化转换率  
        maxHAD :"",  //最大水高
        lowDigit :"", //报警低水位
        HighDigit :"", //报警高水位
        enableAlert:false
      },
      list: [],
    };
  },
  methods: {
    init() {
      try {
        let res = this.communication.getWaterLevelSubmit();
        let list = JSON.parse(res.waterLevelInfoJson) || [];
        let AD=0
        let VAD=0 
        if (Array.isArray(list)) {
       
          list.forEach((item) => {
            if(item.pipe==this.selectRow.pipe){    
              AD=item.AD
              VAD=item.VAD
            }
          });
        } else {
          list = [];
        }
        this.list = list;  
        if(this.selectRow.pipe>0){
          this.selectRow.AD=AD
          this.selectRow.VAD=VAD
        }    
      } catch (error) {}
    },
    itemClick(index){
      console.log(this.list[index])
      let row=JSON.parse(JSON.stringify(this.list[index]))
          row.enableAlert=row.enableAlert?true:false
      this.selectRow=row
    },
    submit() {   
      let data=JSON.parse(JSON.stringify(this.selectRow))
      data.lAD=isNaN(Number(data.lAD))?0:Number(data.lAD)
      data.lADA=isNaN(Number(data.lADA))?0:Number(data.lADA)
      data.hAD=isNaN(Number(data.hAD))?0:Number(data.hAD)
      data.hADA=isNaN(Number(data.hADA))?0:Number(data.hADA)  
      data.enableAlert=  data.enableAlert?1:0
      let res=this.communication.waterLevelSubmit(JSON.stringify(data))
      console.log(res)
    },
    setPipe(pipe) {
      let value1 = pipe % 6 || 6;
      let value2 = Math.ceil(pipe / 6);
      if (value2 == 1) {
        return "A" + value1;
      } else if (value2 == 2) {
        return "B" + value1;
      } else if (value2 == 3) {
        return "C" + value1;
      } else if (value2 == 4) {
        return "D" + value1;
      }
    },
  },
  mounted() {
    setTimeout(() => {
      this.communication = window.Formium.external.communication;
    }, 1000);
  },
};
</script>

<style lang="less" scoped>
.waterLevel {
  padding: 10px;
  .waterLevelTitle {
    padding: 10px;
    font-size: 24px;
  }
  .waterLevelBody{
   width: 100%;
   .waterLevelList{
     display: inline-block;
     width: 30%;
     height: 100%;
     overflow-y: auto;
     vertical-align: middle;
     border: 1px solid #88a3b4;
     margin-right: 10px;
     .waterLeveItem{
      padding: 10px;
      text-align: center;
      border-bottom:  1px solid #88a3b4;
      cursor: pointer;
     }
     .actionWaterLeve{
       background: #1472ec;
       color: #fff;
     }
   }
   .waterLevelCenter{
     position: relative;
     vertical-align: middle;
     display: inline-block;
     width: 67%;
     height: 100%;  
     .centerTitle{
      padding: 10px;
      padding-left: 50px;
      .span{
        display: inline-block;
        width: 50%;
        text-align: center;
      }
     }
     .multiColumn3{
        width: 48%;
        padding: 0px;
        font-size:0px;
        min-width: 0px;
        background: none;
        &>input{
        width: calc(100% - 5px);
        outline: none;
        border: none;
        height: 100%;
        font-size: 19px;
        margin: 0px;  
        padding: 0px;
        padding-left: 5px;
        border-radius: 0px 5px 5px 0px;
    }
     }
   }
  }

  /deep/ .vxe-table .vxe-body--column.col--ellipsis {
    height: 36px !important;
  }
}
</style>