<template>
  <div class="manual" style="height: 100%">
    <div class="manualNav">
      <div class="nav">
        <h1 class="title">
          <img src="../assets/logo.png" width="30" height="40" class="logo" />东恒智能科技助剂称量系统
          <span style="font-size: 15.75px"> {{ dayTime }}</span>
          <span @click="$emit('setWeighingAuthority')" :style="{ color: WeighingAuthority == '1' ? '' : 'red' }">
            {{ WeighingAuthority == "1" ? "已注册" : "未注册" }}</span>
        </h1>
        <span class="btns">
          <!--<vxe-button
            status="primary"
            content="重置"
            @click="ResetData(1)"
          ></vxe-button>-->
          <vxe-button status="primary" content="设置" @click="GetConfig()"></vxe-button>
          <vxe-button status="danger" icon="vxe-icon--close" @click="$emit('close')"></vxe-button>
        </span>
      </div>
    </div>
    <div class="manualBody" :style="'height:' + (height - 60) + 'px'">
      <div class="manualBodyLeft">
        <div v-for="(item, index) of list" :key="index" :class="{
          manualItem: true,
          actionManual: item.pipe == selectRow.pipe,
        }" v-if="index < 18" @click="setSelectRow(item)">
          <div class="waterLevelItemBG" :style="{ top: 100 - (item.PAD || 0) + '%' }"
            :class="{ alarmWaterLevel: item.VAD < config.alarmWaterLevel1 }"></div>
          <div class="manualItem1">
            <p>
              <span class="pipe" :class="{selectPipe:item.pipe==selectRow.pipe}">{{ setPipe(item.pipe) }}</span>
              <span class="itemVAD">{{ setToFixed(item.VAD) }}{{ config.WaterLevelNuit }}</span>
            </p>
            <p class="code">{{ item.code }}</p>
            <p class="codeName">{{ item.codeName }}</p>
          </div>
        </div>
      </div>
      <div class="manualBodyRight" :style="{ height: height - 140 * 3 - 55 + 'px' }">
        <div class="pattern">
          <!-- 非料单模式  -->
          <p class="patternItem">非</p>
          <p class="patternItem">料</p>
          <p class="patternItem">单</p>
          <p class="patternItem">模</p>
          <p class="patternItem">式</p>
          <input ref="demandWeight_" style="display: none" />
        </div>
        <div class="BlankingTask">
          <div class="multiColumn" style="margin-bottom: 10px">
            <div style="width: 100px">物料名称</div>
            <div style="font-size: 20px; padding-left: 5px">
              {{ selectRow.codeName }}
            </div>
          </div>
          <div class="multiColumn" style="margin-bottom: 10px">
            <div style="width: 100px">物料编码</div>
            <div style="font-size: 20px; padding-left: 5px">
              {{ selectRow.code }}
            </div>
          </div>
          <div class="multiColumn" style="margin-bottom: 10px">
            <div style="width: 100px">目标重量</div>
            <div>
              <input ref="demandWeight" type="text" v-model="demandWeight" style="color: red" @blur="setFocus" />
            </div>
            <div style="width: 50px">{{ config.viewUnit || 'kg' }}</div>
          </div>
          <div class="multiColumn" style="margin-bottom: 10px">
            <div style="width: 100px">实际重量</div>
            <div style="
                font-size: 28px;
                padding-left: 5px;
                font-weight: bold;
                color: red;
              ">
              {{ setKg(WeighingInformation[selectRow.address].total) }}
            </div>
            <div style="width: 50px">{{ config.viewUnit || 'kg' }}</div>
          </div>
          <div class="multiColumn" style="margin-bottom: 10px">
            <div style="width: 100px">差 异 量</div>
            <div style="font-size: 20px; padding-left: 5px">
              {{ setKg(WeighingInformation[selectRow.address].difference) }}
            </div>
          </div>
        </div>
        <div class="manualBodycall">
          <div class="callItemCall">
            <img class="imgs" :src="
              setImage(
                WeighingInformation[selectRow.address].feedingStatus,
                selectRow.address
              )
            " alt="" />
            <div class="portResult">
              {{ setKg(portResult[selectRow.address]) }}
              <span class="portResultUnit">{{ config.viewUnit || 'kg' }}</span>
            </div>
            <div class="WeighingInfoClass">
              {{
                  setWeighingInfo(
                    WeighingInformation[selectRow.address].feedingStatus
                  )
              }}
            </div>
            <div class="callAggregateValue">
              <div class="items">
                <p>目标重量</p>
                <p class="itemValue">
                  <span v-if="
                    WeighingInformation[selectRow.address].feedingStatus != -1
                  ">{{
    setKg(WeighingInformation[selectRow.address].demandWeight)
}}</span>
                </p>
              </div>
              <div class="items">
                <p>实际称量</p>
                <p class="itemValue" style="color: red">
                  <span v-if="
                    WeighingInformation[selectRow.address].feedingStatus != -1
                  ">{{
    setKg(WeighingInformation[selectRow.address].total)
}}</span>
                </p>
              </div>
              <div class="items">
                <p>差&nbsp;&nbsp;异&nbsp;&nbsp;值</p>
                <p class="itemValue">
                  <span v-if="
                    WeighingInformation[selectRow.address].feedingStatus != -1
                  ">{{
    setKg(WeighingInformation[selectRow.address].difference)
}}</span>
                </p>
              </div>
            </div>
            <div class="itemMadol" v-show="
              WeighingInformation[selectRow.address].feedingStatus == 4 ||
              WeighingInformation[selectRow.address].feedingStatus == 8 ||
              WeighingInformation[selectRow.address].feedingStatus == 2 ||
              WeighingInformation[selectRow.address].feedingStatus == 3
            ">
              <div style="padding: 15px; color: red; font-size: 18px">
                {{
                    WeighingInformation[selectRow.address].feedingStatus == 4
                      ? "加料异常"
                      : WeighingInformation[selectRow.address].feedingStatus == 8
                        ? "已完成"
                        : WeighingInformation[selectRow.address].feedingStatus ==
                          2 ||
                          WeighingInformation[selectRow.address].feedingStatus == 3
                          ? "已满;请换桶"
                          : ""
                }}
              </div>
              <div style="text-align: center" v-show="
                WeighingInformation[selectRow.address].feedingStatus == 8
              ">
                <vxe-button status="primary" content="确定" @click="ResetData(2)"></vxe-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <vxe-modal v-model="show" width="600" show-footer>
      <template #default>
        <div>
          <div class="multiColumn">
            <div>物料名称</div>
            <div>{{ selectRow.pipeName }}</div>
          </div>
          <div class="multiColumn">
            <div>物料编码</div>
            <div>{{ selectRow.pipeCode }}</div>
          </div>
          <div class="multiColumn">
            <div>需求重量</div>
            <div>{{ selectRow.quantity }}</div>
          </div>
          <p>
            <vxe-button status="primary" content="确定" @click="submit"></vxe-button>
          </p>
        </div>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
import a1 from "../assets/a1.bmp";
import a2 from "../assets/a2.bmp";
import a3 from "../assets/a3.bmp";
import a4 from "../assets/a4.bmp";
export default {
  props: [
    "value",
    "config",
    "focusLock",
    "modeSwitch"
  ],
  data() {
    return {
      imgs: [a1, a2, a3, a4],
      height:
        document.documentElement.clientHeight || document.body.clientHeight,
      communication: {},
      show: false,
      dayTime: new Date().toLocaleString(),
      list: [],
      selectRow: {
        code: "", // 助剂编号
        codeName: "", // 助剂名称
        pipe: "0", // 管道号
        address: 1, // 称地址
        flow: "0", //流量系数
        AD: "0", //模拟输入值
        VAD: "0", //显示水高
        lAD: "0", //低端AD
        lADA: "0", //低端水高
        hAD: "0", //高端AD
        hADA: "0", //高端水高
        PAD: "0", //百分比数据
        ConversionRate: "0", //变化转换率
        maxHAD: "0", //最大水高
        lowDigit: "0", //报警低水位
        HighDigit: "0", //报警高水位
      },
      demandWeight: 0,
      ManualTasksSwitchLock: 0,
      portResult: [0, 0, 0, 0],
      WeighingInformation: [
        { total: 0 },
        { total: 0 },
        { total: 0 },
        { total: 0 },
      ], //当前执行中的料单信息
      recordShow: false,
      WeighingAuthority: "0"
    };
  },
  watch: {
    value(n, o) {
      this.selectRow = {
        code: "", // 助剂编号
        codeName: "", // 助剂名称
        pipe: "0", // 管道号
        address: 1, // 称地址
        flow: "0", //流量系数
        AD: "0", //模拟输入值
        VAD: "0", //显示水高
        lAD: "0", //低端AD
        lADA: "0", //低端水高
        hAD: "0", //高端AD
        hADA: "0", //高端水高
        PAD: "0", //百分比数据
        ConversionRate: "0", //变化转换率
        maxHAD: "0", //最大水高
        lowDigit: "0", //报警低水位
        HighDigit: "0", //报警高水位
      }
    }
  },
  methods: {
    //获取物料信息
    init() {
      try {
        let res = __communication.queryData();
        let resultData = JSON.parse(res.resultData) || { code: -1 };

        let {
          fromData,
          COMStart,
          portResult,
          portPLCResultStart,
          dataStart,
          code,
          modeSwitch,
          ManualTasksSwitchLock,
          waterLevelInfoJson,
          manualTasksJson,
          WeighingAuthority
        } = resultData;
        if (code == -1) {
          return;
        }
        if (this.value != modeSwitch) {
          this.$emit("input", modeSwitch);
        }
        this.modeSwitch=modeSwitch
        if (modeSwitch != 2) {
          return this.$emit("initFocus");
        }
        this.WeighingAuthority = WeighingAuthority
        let feedingStatus = this.WeighingInformation[1].feedingStatus;
        if (feedingStatus != fromData.WeighingInformation[1].feedingStatus) {
          console.log(fromData.WeighingInformation, this.selectRow.address);
        }
        this.WeighingInformation = fromData.WeighingInformation || [
          { total: 0 },
          { total: 0 },
          { total: 0 },
          { total: 0 },
        ];
        this.portResult = portResult;
        this.ManualTasksSwitchLock = ManualTasksSwitchLock;
        let waterLevelInfo = JSON.parse(waterLevelInfoJson) || [];
        waterLevelInfo.forEach((item) => {
          let PAD =
            this.config.maxWaterLevel > 0 && item.VAD != 0
              ? item.VAD / this.config.maxWaterLevel
              : 0;
          PAD = PAD > 1 ? 1 : PAD;
          item.PAD = PAD < 0 ? 0 : PAD * 100;
        });
        this.list = waterLevelInfo;
        let bool=true
          for (let index = 0; index < this.WeighingInformation.length; index++) {
            const element = this.WeighingInformation[index];
            let feedingStatus1=element.feedingStatus
            if(feedingStatus1>1 && feedingStatus1<8){
              bool=false
            }
          }
          if(bool){
            let pipeInfo = {};
            if (manualTasksJson) {
              let manualTasks = JSON.parse(manualTasksJson) || [];
              pipeInfo = manualTasks[manualTasks.length - 1] || {};
            }
            if (pipeInfo.pipe) {
              this.selectRow = waterLevelInfo[pipeInfo.pipe - 1];
            } else {
              this.selectRow = this.$options.data().selectRow;
            }
          }
      } catch (error) {
        console.log(error);
      }
      this.setFocus();
      this.dayTime = new Date().toLocaleString();
      this.$emit("manualLockOk")
    },
    //料桶检测
    BarrelDetection() { },
    setSelectRow(item){
      __communication.addManual(""+item.pipe);
    },
    //确定下料
    submit() {
      if (
        this.selectRow.pipe > 0 &&
        this.demandWeight > 0 &&
        this.ManualTasksSwitchLock == 1
      ) {
        let config = this.config || {}
        let unit = config.viewUnit || 'kg'
        unit = unit.toUpperCase()
        let unitNumbel = 1
        switch (unit) {
          case 'G':
            unitNumbel = 1;
            break;
          case 'KG':
            unitNumbel = 1000;
            break;
          default:
            unitNumbel = 1
            break;
        }
        let obj = {
          pipe: this.selectRow.pipe,
          demandWeight: parseInt(this.demandWeight * unitNumbel),
        };
        __communication.manualSubmit(JSON.stringify(obj));
      }
    },
    GetConfig() {
      this.$emit("GetConfig");
    },
    setPipe(pipe) {
      let value1 = pipe % 6 || 6;
      let value2 = Math.ceil(pipe / 6);
      if (value2 == 1) {
        return "A" + value1;
      } else if (value2 == 2) {
        return "B" + value1;
      } else if (value2 == 3) {
        return "C" + value1;
      } else if (value2 == 4) {
        return "D" + value1;
      }
    },
    setFocus() {
      try {
        console.log("手动焦点",this.focusLock,this.modeSwitch!=2) 
        if (this.focusLock || this.modeSwitch!=2) {
          return;
        }
        this.$refs.demandWeight.focus();
      } catch (error) { }
    },
    setKg(num) {
      let value = num;
      try {
        let config = this.config || {}
        let decimalPlaces = config.decimalPlaces || 0
        let unit = config.viewUnit || 'kg'
        unit = unit.toUpperCase()
        let unitNumbel = 1
        switch (unit) {
          case 'G':
            unitNumbel = 1;
            break;
          case 'KG':
            unitNumbel = 1000;
            break;
          default:
            unitNumbel = 1
            break;
        }
        value = Number(num / unitNumbel).toFixed(decimalPlaces) || 0;
      } catch (error) {
        value = 0;
      }
      if (value == "NaN") {
        value = "";
      }
      return value;
    },
    setToFixed(num) {
      let value = num;
      try {
        value = Number(num).toFixed(1) || 0;
      } catch (error) {
        value = 0;
      }
      if (value == "NaN") {
        value = "";
      }
      return value;
    },
    setImage(num, callIndex) {
      let config = this.config || {}
      let BarrelWeight = config.barrelWeight || 150
      let value = this.portResult[callIndex];
      let imgs = this.imgs;
      let img = imgs[0];
      switch (num) {
        case -1:
        case 0:
          if (value > BarrelWeight) {
            img = imgs[1];
          } else {
            img = imgs[0];
          }
          break;
        case 1:
          img = imgs[1];
          break;
        case 4:
        case 5:
        case 6:
        case 7:
          img = imgs[2];
          break;
        case 2:
        case 3:
        case 8:
          img = imgs[3];
          break;
        default:
          break;
      }
      return img;
    },
    setWeighingInfo(num) {
      let str = "";
      switch (num) {
        case -1:
          str = "空闲中";
          break;
        case 0:
          str = "请放桶";
          break;
        case 1:
          str = "准备完毕";
          break;
        case 2:
        case 3:
          str = "已满";
          break;
        case 4:
          str = "加料异常";
          break;
        case 5:
          str = "加料中";
          break;
        case 6:
        case 7:
          str = "保存记录";
          break;
        case 8:
          str = "已完成";
          break;
        default:
          break;
      }
      return str;
    },
    //重置称量状态
    ResetData() {
      let address = this.selectRow.address + "" || "0";
      __communication.ViewEnter(address + "");
    },
    key__fn(e) {
      switch (e.keyCode) {
        case 13:
          this.submit();
          let address = this.selectRow.address + "" || "0";
          __communication.ViewEnter(address);
          break;
        case 27:
          let WeighingInformation=this.WeighingInformation
          let bool=true
          for (let index = 0; index < WeighingInformation.length; index++) {
            const element = WeighingInformation[index];
            let feedingStatus=element.feedingStatus
            if(feedingStatus>1 && feedingStatus<8){
              bool=false
            }
          }
          if(bool){
            this.demandWeight="0"
            __communication.manualOff("");
          }

          break;
      }
    },
  },
  mounted() { },
};
</script>

<style lang="less" scoped>
.manual {
  .nav {
    position: relative;
    height: 55px;
    padding: 8px 0px;
    box-sizing: border-box;
    background: linear-gradient(131deg, #021c2f 0%, #0e92d5 100%);

    .title {
      margin: 0px;
      padding: 2px 0px;
      color: #fff;
      font-size: 26px;
      font-family: Source Han Sans CN;
      font-weight: bold;
      color: #ffffff;

      .logo {
        vertical-align: middle;
        margin: 0px 5px 0px 10px;
      }

      .nonMaterial {
        font-size: 20px;
        padding: 5px 8px;
        border: 1px solid #fff;
        border-radius: 5px;
        margin-left: 10px;
      }
    }

    .btns {
      position: absolute;
      right: 10px;
      top: 10px;
    }
  }

  .manualBody {
    //white-space: nowrap;
    box-sizing: border-box;
    width: 100%;

    .manualBodyLeft {
      box-sizing: border-box;
      display: inline-block;
      vertical-align: middle;
      width: 100%;
      //height: 100%;
      padding-top: 5px;
      white-space: normal;
      text-align: center;

      .manualItem {
        position: relative;
        display: inline-block;
        box-sizing: border-box;
        width: calc(16% - 8px);
        margin-left: 5px;
        margin-bottom: 5px;
        height: 130px;
        text-align: center;
        padding: 5px 0px;
        border: 1px solid #8294a1;
        border-radius: 5px;
        vertical-align: middle;
        overflow: hidden;

        .manualItem1 {
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 1;
        }

        .pipe {
          display: inline-block;
          width: 40px;
          height: 40px;
          line-height: 40px;
          font-size: 24px;
          border-radius: 50%;
          background: #0e54d5;
          color: #fff;
          vertical-align: middle;
        }
        .selectPipe{
          background: #eb09d8;
        }

        .itemVAD {
          display: inline-block;
          margin-left: 5px;
          width: calc(100% - 55px);
          height: 38px;
          line-height: 38px;
          border-radius: 5px;
          vertical-align: middle;
          box-shadow: 0px 0px 4px 0px #6e8150;
        }

        .code {
          padding: 5px;
        }

        .codeName {
          padding: 5px;
          white-space: nowrap;
          overflow: hidden;
        }
      }

      .actionManual {
        border: 1px solid #0a19e9;
      }
    }

    .manualBodyRight {
      box-sizing: border-box;
      display: inline-block;
      vertical-align: middle;
      width: 96%;
      padding: 5px 10px;
      margin-left: 2%;
      box-sizing: border-box;
      white-space: nowrap;

      .pattern {
        display: inline-block;
        box-sizing: border-box;
        height: 99%;
        width: 45px;
        text-align: center;
        font-size: 30px;
        border: 1px solid #021c2f;
        border-radius: 5px;
        margin-right: 5px;
        vertical-align: middle;

        .patternItem {
          text-align: center;
          padding: 5px 0px;
        }
      }

      .BlankingTask {
        display: inline-block;
        height: 100%;
        width: 420px;
        padding-top: 10px;
        vertical-align: middle;

        .multiColumn {
          height: 44px;
          line-height: 42px;

          &> :nth-child(1) {
            font-size: 22px;
          }

          &> :nth-child(2) {
            overflow: hidden;

            &>input {
              font-size: 28px;
              font-weight: bold;
            }
          }

          &> :nth-child(3) {
            font-size: 24px;
          }
        }
      }

      .manualBodycall {
        display: inline-block;
        height: 100%;
        width: 320px;
        vertical-align: middle;
        border: 1px solid #a1a1a1;
        border-radius: 5px;
        margin-left: 10px;
        overflow: hidden;

        .callItemCall {
          position: relative;
          height: 100%;
          width: 100%;

          .imgs {
            position: absolute;
            height: 100%;
            width: auto;
            right: 0px;
            top: 0px;
          }

          .callAggregateValue {
            padding-top: 10px;
            height: 300px;
            width: 85px;
            padding-left: 30px;

            .items {
              height: 68px;

              p {
                text-align: center;
                font-weight: bold;
                font-size: 20px;
              }

              .itemValue {
                color: #116be9;
                font-size: 28px;
              }
            }
          }

          .mainPageWaterLevel {
            position: absolute;
            height: 100%;
            width: 90px;
            top: 6px;
            left: 0px;
            z-index: 1;

            .waterLevelItem {
              position: relative;
              display: inline-table;
              vertical-align: middle;
              width: 40px;
              height: 32%;
              box-sizing: border-box;
              border: 1px solid #a1a1a1;
              border-radius: 4px;
              padding-top: 10px;
              margin-bottom: 3px;
              margin-left: 3px;
              text-align: center;
              background: #ffffff;
              overflow: hidden;

              .waterLevelItemValue {
                position: absolute;
                width: 40px;
                height: 20px;
                font-size: 14px;
                overflow: hidden;
                left: 0px;
                bottom: 50%;
                z-index: 1;
              }

              .waterLevelItemName {
                position: absolute;
                font-size: 20px;
                width: 40px;
                left: 0px;
                bottom: 5px;
                z-index: 1;
              }
            }
          }
        }

        .callText {
          position: absolute;
          z-index: 2;
          width: 100%;
          height: 100%;
          padding: 5px;

          .callTextItem {
            text-align: start;
            font-size: 16px;
            margin: 0px;
            height: 26px;
            line-height: 26px;
            margin-top: 6px;
            border-radius: 4px;
            width: calc(100% - 150px);
            overflow: hidden;
            border: 1px solid #88a3b4;

            span {
              display: inline-block;
              font-weight: bold;
            }

            .callTextLabel {
              text-align: end;
              width: 70px;
              padding-right: 3px;
              margin-right: 3px;
              border-right: 1px solid #88a3b4;
            }
          }
        }

        .portResult {
          position: absolute;
          box-sizing: border-box;
          bottom: 10px;
          right: 25px;
          width: 150px;
          border-radius: 4px;
          font-weight: bold;
          height: 40px;
          font-size: 34px;
          font-family: Source Han Sans CN;
          font-weight: bold;
          color: red;
          box-shadow: 0px 0px 5px 0px #88a3b4 inset;
          line-height: 40px;
          overflow: hidden;
          white-space: nowrap;
          text-align: center;

