<template>
  <div class="body" @click="bodyClick" :style="'height:' + (height - 1) + 'px'">
    <SimulatedEnvironment v-model="SimulationTestShow" :communication="communication"></SimulatedEnvironment>
    <div v-show="WeighingAuthorityShow" class="modal">
      <div class="modal-body">
        <div class="modal-title">注册</div>
        <div style="padding:0px 10px">
          <div style="margin-bottom: 10px; text-align: center">
            <div class="input">
              <span class="label" style="width: 70px">账号</span>
              <span class="value"><input disabled v-model="config.EquipmentIdentification" /></span>
            </div>
            <p></p>
            <div class="input">
              <span class="label" style="width: 70px">注册码</span>
              <span class="value"><input v-model="config.EquipmentIdentificationCode" /></span>
            </div>
          </div>
          <div style="text-align: end;">
            <vxe-button content="取消" @click="WeighingAuthorityShow = false"></vxe-button>
            <vxe-button status="primary" content="确定" @click="setWeighingAuthority_"></vxe-button>
          </div>
        </div>
      </div>
    </div>
    <div v-show="modelShow" class="modal">
      <div class="modal-body">
        <div class="modal-title">提示</div>
        <div style="padding: 10px">
          <div style="margin-bottom: 10px; text-align: center">
            <vxe-radio-group v-model="tempFocus">
              <vxe-radio :label="5" content="称量配置"></vxe-radio>
              <vxe-radio :label="6" content="数据配置"></vxe-radio>
              <vxe-radio :label="7" content="称量记录"></vxe-radio>
            </vxe-radio-group>
          </div>
          <div class="input" v-if="tempFocus != 7">
            <span class="label" style="width: 70px">密码</span>
            <span class="value"><input type="password" v-model="upwd" /></span>
          </div>
          <div style="text-align: end; padding-top: 10px">
            <vxe-button content="取消" @click="modelShow = false"></vxe-button>
            <vxe-button status="primary" content="确定" @click="madolOk"></vxe-button>
          </div>
        </div>
      </div>
    </div>
    <div v-show="dyeingClassShow" class="modal">
      <div class="modal-body" style="height: 300px">
        <div class="modal-title">请选择当前料单的编号</div>
        <div style="padding: 10px">
          <div style="height: 195px">
            <vxe-select v-model="RequltData.dyeingClass" placeholder="请选择" ref="vxeSelect">
              <vxe-option v-for="(item, index) of dyeingClassList" :key="index" :value="item" :label="item">
              </vxe-option>
            </vxe-select>
          </div>
          <div style="text-align: end">
            <vxe-button status="primary" content="确定" @click="dyeingClassSave"></vxe-button>
          </div>
        </div>
      </div>
    </div>
    <div v-show="CompletionPrompt" class="modal">
      <div class="modal-body">
        <div class="modal-title">提示</div>
        <div style="padding: 10px">
          <div style="
              margin-bottom: 15px;
              text-align: center;
              font-weight: bold;
              font-size: 30px;
            ">
            单前料单已完成
          </div>
          <div style="text-align: end; padding-top: 10px">
            <vxe-button status="primary" content="确定" @click="CompletionPrompt = false"></vxe-button>
          </div>
        </div>
      </div>
    </div>
    <div v-show="manualLockShow" class="modal">
      <div class="modal-body">
        <div class="modal-title">提示</div>
        <div style="padding: 10px">
          <div class="input">
            <span class="label" style="width: 70px">密码</span>
            <span class="value"><input type="password" v-model="manualLockUpwd" /></span>
          </div>
          <div style="text-align: end; padding-top: 10px">
            <vxe-button content="取消" @click="manualLockShow = false"></vxe-button>
            <vxe-button status="primary" content="确定" @click="manualLockOk(1)"></vxe-button>
          </div>
        </div>
      </div>
    </div>
    <div v-show="focus < 5">
      <div class="nav">
        <h1 class="title">
          <img src="../assets/logo.png" width="30" height="40" class="logo" />东恒智能科技助剂称量系统
          <span style="font-size: 15.75px"> {{ dayTime }}</span>
          <span @click="setWeighingAuthority" :style="{ color: WeighingAuthority == '1' ? '' : 'red' }"> {{
              WeighingAuthority == "1" ? "已注册" : "未注册"
          }}</span>

        </h1>

        <span class="btns">
          <vxe-button status="primary" content="重置" @click="ResetData(1)"></vxe-button>
          <vxe-button status="primary" content="设置" @click="GetConfig()"></vxe-button>
          <vxe-button status="danger" icon="vxe-icon--close" @click="close"></vxe-button>
        </span>
      </div>
      <!-- 头部信息 -->
      <div class="row" style="padding-left: 10px; margin-top: 10px">
        <!-- 单头信息 -->
        <div class="col" :style="
          'width:33.33%;padding-right:10px;height:' + height * 0.3 + 'px'
        ">
          <div class="input m_b10">
            <span class="label">料单号</span>
            <span class="value">
              <input ref="itemNo" id="itemNo" type="text" style="color: #1176d4; font-size: 25px; font-weight: bold"
                @blur="blur" @focus="blur" @dblclick="inputDblclick" v-model="itemNo" />
              <input type="text" ref="itemNo_off" class="itemNo_off" />
            </span>
          </div>
          <div class="input m_b10">
            <span class="label">布&nbsp;&nbsp;&nbsp;&nbsp;重</span>
            <span class="value" style="color: #1176d4; font-size: 25px; font-weight: bold">{{ formData.clothWeight || ""
            }}
              <span v-if="formData.clothWeight">{{
                  formData.unit || ""
              }}</span></span>
          </div>
          <div class="input m_b10">
            <span class="label">颜&nbsp;&nbsp;&nbsp;&nbsp;色</span>
            <span class="value" style="color: #1176d4; font-size: 25px; font-weight: bold">{{ formData.color || ""
            }}</span>
          </div>
          <div>
            <p style="padding: 6px 0px">
              <span class="communicationStatus">后台服务状态
                <i class="vxe-icon--refresh roll c_g" v-if="dataStart"></i>
                <i class="vxe-icon--warning c_r" v-else></i></span>
              <span class="communicationStatus" style="padding-right: 10px; text-align: end">
                PLC状态
                <i class="vxe-icon--refresh roll c_g" v-if="COMStart[0] < 6"></i>
                <i class="vxe-icon--warning c_r" v-else></i>
              </span>
            </p>
            <p></p>
            <p class="callStart">
              <span class="callStartItem" style="text-align: start">
                A称
                <i class="vxe-icon--refresh roll c_g" v-if="COMStart[1] < 6"></i>
                <i class="vxe-icon--warning c_r" v-else></i>
              </span>
              <span class="callStartItem">
                B称
                <i class="vxe-icon--refresh roll c_g" v-if="COMStart[2] < 6"></i>
                <i class="vxe-icon--warning c_r" v-else></i>
              </span>
              <span class="callStartItem" style="text-align: end; padding-right: 10px">
                C称
                <i class="vxe-icon--refresh roll c_g" v-if="COMStart[3] < 6"></i>
                <i class="vxe-icon--warning c_r" v-else></i>
              </span>
            </p>
            <p class="resInfo">响应信息:{{ formData.message }}</p>
          </div>
        </div>

        <!-- 明细信息 -->
        <div class="col detailed" :style="
          'width:66.6%;padding-right:10px;height:' + height * 0.3 + 'px'
        ">
          <vxe-table border ref="detailedTable" show-header-overflow show-overflow align="center"
            @cell-click="cellClick" :data="list" :height="height * 0.3 - 10" :row-class-name="rowClassName">
            <vxe-column type="seq" title="序号" width="60"></vxe-column>
            <vxe-column field="code" title="物料编号" width="100"></vxe-column>
            <vxe-column field="codeName" title="物料名称"></vxe-column>
            <vxe-column field="demandWeight" title="用量" width="120"></vxe-column>
            <vxe-column field="unit" title="单位" width="100"></vxe-column>
          </vxe-table>
        </div>
      </div>
      <!-- 称 -->
      <div class="row t_a_c" style="padding-left: 10px">
        <div class="col" v-for="(item, index) of listAll" :key="index" v-if="index > 0" :style="{
          width: '33.3%',
          paddingRight: '10px',
          height: height * 0.68 - 70 + 'px',
        }">
          <vxe-table border show-header-overflow show-overflow align="center" :data="listAll[index]"
            @cell-click="cellClick" :height="160" :row-class-name="rowClassName">
            <vxe-column field="code" title="物料编号" width="80"></vxe-column>
            <vxe-column field="codeName" title="物料名称"></vxe-column>
            <vxe-column field="demandWeight" title="用量" width="90"></vxe-column>
            <vxe-column field="unit" title="单位" width="60"></vxe-column>
          </vxe-table>
          <div class="callItem">
            <div class="callItemHaed">
              <span class="callNum">{{ setAddress(index) }}</span>
              <p>
                <span>物料名称:</span><span v-if="formData.WeighingInformation[index].feedingStatus != -1">{{
                    formData.WeighingInformation[index].codeName
                }}</span>
              </p>
              <p>
                <span>物料编码:</span><span v-if="formData.WeighingInformation[index].feedingStatus != -1">{{
                    formData.WeighingInformation[index].code
                }}</span>
              </p>
            </div>
            <div class="callItemCall">
              <img class="imgs" :src="
                setImage(
                  formData.WeighingInformation[index].feedingStatus,
                  index
                )
              " alt="" />
              <div class="portResult">{{ setKg(portResult[index]) }} <span
                  class="portResultUnit">{{ config.viewUnit || 'kg' }}</span></div>
              <div class="WeighingInfoClass">
                {{
                    setWeighingInfo(
                      formData.WeighingInformation[index].feedingStatus
                    )
                }}
              </div>
              <div class="callAggregateValue">
                <div class="items">
                  <p>目标重量</p>
                  <p class="itemValue">
                    <span v-if="
                      formData.WeighingInformation[index].feedingStatus != -1
                    ">{{
    setKg(formData.WeighingInformation[index].demandWeight)
}}</span>
                  </p>
                </div>
                <div class="items">
                  <p>实际称量</p>
                  <p class="itemValue" style="color: red">
                    <span v-if="
                      formData.WeighingInformation[index].feedingStatus != -1
                    ">{{
    setKg(formData.WeighingInformation[index].total)
}}</span>
                  </p>
                </div>
                <div class="items">
                  <p>差&nbsp;&nbsp;异&nbsp;&nbsp;值</p>
                  <p class="itemValue">
                    <span v-if="
                      formData.WeighingInformation[index].feedingStatus != -1
                    ">{{
    setKg(formData.WeighingInformation[index].difference)
}}</span>
                  </p>
                </div>
              </div>
              <div class="mainPageWaterLevel">
                <div class="waterLevelItem" v-for="item_ in 6" key="item_">
                  <div class="waterLevelItemBG" :style="{ top: 100 - waterLevelList[((index - 1) * 6 + item_ - 1)].PAD + '%' }"
                    :class="{ alarmWaterLevel: waterLevelList[((index - 1) * 6 + item_ - 1)].VAD < config.alarmWaterLevel1 }"></div>
                  <p class="waterLevelItemValue">{{ $toFixed_1(waterLevelList[((index - 1) * 6 + item_ - 1)].VAD) }}</p>
                  <p class="waterLevelItemName">{{ index == 1 ? 'A' : index == 2 ? 'B' : 'C' }}{{ item_ }}</p>
                </div>
              </div>
            </div>
            <div class="itemMadol" :class="focus == index ? 'itemMadol_focus' : ''" v-show="itemMadolShow[index]"
              :ref="'itemMadol' + index">
              <div style="padding: 15px">
                {{
                    formData.WeighingInformation[index].feedingStatus == 4
                      ? "加料异常"
                      : formData.WeighingInformation[index].feedingStatus == 8
                        ? "已完成"
                        : formData.WeighingInformation[index].feedingStatus == 2 ||
                          formData.WeighingInformation[index].feedingStatus == 3
                          ? "已满;请换桶"
                          : ""
                }}
              </div>
              <div style="text-align: center">
                <vxe-button status="primary" content="确定" @click="ResetData(2)"></vxe-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-show="focus == 5" class="config">
      <!-- 称量配置 -->
      <p>
        称量参数配置 &nbsp;&nbsp;&nbsp;<vxe-checkbox v-model="config.dyeingClass" content="加色/补色合并时提示"></vxe-checkbox>
      </p>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">桶容量</span>
          <span class="value">
            <input v-model="config.capacity" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">误差量</span>
          <span class="value">
            <input v-model="config.allowableError" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">减速位</span>
          <span class="value">
            <input v-model="config.reduce" />
          </span>
        </div>
      </span>


      <span class="item15">
        <div class="input m_b10">
          <span class="label">稳定时间</span>
          <span class="value">
            <input v-model="config.StopTime" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">稳定系数</span>
          <span class="value">
            <input v-model="config.stableValue" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label" style="width: 105px;white-space: nowrap;">稳定系数(小)</span>
          <span class="value">
            <input v-model="config.minStableValue" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">小数点位数</span>
          <span class="value">
            <input v-model="config.decimalPlaces" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">称系数</span>
          <span class="value">
            <input v-model="config.decimalPlaces1" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">显示单位</span>
          <span class="value">
            <input v-model="config.viewUnit" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">补偿量</span>
          <span class="value">
            <input v-model="config.compensate" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">补偿系数</span>
          <span class="value">
            <input v-model="config.compensationCoefficient" />
          </span>
        </div>
      </span>
     <span class="item15">
        <div class="input m_b10">
          <span class="label">小阀值</span>
          <span class="value">
            <input v-model="config.smallValve" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">低水位</span>
          <span class="value">
            <input v-model="config.alarmWaterLevel1" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">高水位</span>
          <span class="value">
            <input v-model="config.alarmWaterLevel2" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">满水位</span>
          <span class="value">
            <input v-model="config.maxWaterLevel" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">水位单位</span>
          <span class="value">
            <input v-model="config.WaterLevelNuit" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">手动密码</span>
          <span class="value">
            <input v-model="config.manualLock" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">桶重量</span>
          <span class="value">
            <input v-model="config.barrelWeight" />
          </span>
        </div>
      </span>
      <span class="item15">
        <div class="input m_b10">
          <span class="label">语速</span>
          <span class="value">
            <input v-model="config.rate" />
          </span>
        </div>
      </span>
       <span class="item15">
        <div class="input m_b10">
          <span class="label">播报次数</span>
          <span class="value">
            <input v-model="config.rateNum" />
          </span>
        </div>
      </span>
      <p>管道设置</p>
      <!-- 管道设置 -->
      <vxe-table border show-header-overflow show-overflow align="center" :data="config.materials"
        :height="height - 310" :edit-config="{ trigger: 'click', mode: 'cell' }">
        <vxe-column type="seq" title="序号" width="60"></vxe-column>
        <vxe-column field="pipe" title="管道号">
          <template v-slot="{ row, rowIndex }">
            <span>
              {{ setPipe(row.pipe) }}
            </span>
          </template>
        </vxe-column>
        <vxe-column field="code" title="助剂编号" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="codeName" title="助剂名称" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="flow" title="流量系数" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>

        <vxe-column field="minFlow" title="流量系数(小)" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="reduce" title="减速位" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="minReduce" title="减速位(小)" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="startBit" title="小阀启动位" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="StopTime" title="稳定时间(小)" :edit-render="{
          name: 'input',
          attrs: { type: 'text' },
          autoselect: true,
        }"></vxe-column>
        <vxe-column field="blankingAlarm" title="下料异常报警">
          <template v-slot="{ row, rowIndex }">
            <span>
              <vxe-checkbox v-model="row.blankingAlarm" content="" :checked-value="1" :unchecked-value="0" ></vxe-checkbox>
            </span>
          </template>
        </vxe-column>
      </vxe-table>
      <span class="submit">
        <vxe-button status="primary" content="返回" @click="focus = 0"></vxe-button>
        <vxe-button status="primary" content="水位效正" @click="focus = 8"></vxe-button>
        <vxe-button status="primary" content="保存关闭" @click="SetConfig(1)"></vxe-button>
      </span>
    </div>
    <div v-show="focus == 6" class="config">
      <p>ERP数据对接配置</p>
      <span class="item15">
        <div class="input m_b10" style="overflow: visible">
          <span class="label">对接方式</span>
          <span class="value vxeSelect">
            <vxe-select v-model="config.way" size="medium">
              <vxe-option v-for="(item, index) of config.ways" :key="index" :value="item.way" :label="item.name">
              </vxe-option>
            </vxe-select>
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">用户</span>
          <span class="value">
            <input v-model="config.user" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">密码</span>
          <span class="value">
            <input v-model="config.upwd" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">IP地址</span>
          <span class="value">
            <input v-model="config.ip" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">端口号</span>
          <span class="value">
            <input v-model="config.port" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">库名称</span>
          <span class="value">
            <input v-model="config.tableName" />
          </span>
        </div>
      </span>

      <span class="item15">
        <div class="input m_b10">
          <span class="label">PLC版本号</span>
          <span class="value">
            <input v-model="config.verstionPlc" />
          </span>
        </div>
      </span>

      <span class="item15" style="width: 98%">
        <div class="input m_b10">
          <span class="label">登录/单头</span>
          <span class="value">
            <input v-model="config.connect1" />
          </span>
        </div>
      </span>

      <span class="item15" style="width: 98%">
        <div class="input m_b10">
          <span class="label">物料列表</span>
          <span class="value">
            <input v-model="config.connect2" />
          </span>
        </div>
      </span>

      <span class="item15" style="width: 98%">
        <div class="input m_b10">
          <span class="label">完成回调</span>
          <span class="value">
            <input v-model="config.connect3" />
          </span>
        </div>
      </span>
      <!-- 串口设置 -->
      <p>串口设置</p>
      <vxe-table border show-header-overflow show-overflow align="center" :data="config.fns"
        :edit-config="{ trigger: 'click', mode: 'cell' }">
        <vxe-column type="seq" title="序号" width="60"></vxe-column>
        <vxe-column field="address" title="功能名称">
          <template v-slot="{ row, rowIndex }">
