  </div>
</template>

<script>
import a1 from "../assets/a1.bmp";
import a2 from "../assets/a2.bmp";
import a3 from "../assets/a3.bmp";
import a4 from "../assets/a4.bmp";
import SimulatedEnvironment from "../components/SimulatedEnvironment.vue";
import Manual from "../components/Manual.vue";
import WaterLevel from "../components/WaterLevel.vue";
export default {
  name: "index",
  components: {
    SimulatedEnvironment,
    Manual,
    WaterLevel
  },
  data() {
    return {
      modelShow: false,
      manualLockShow: false,
      WeighingAuthority: "0",
      WeighingAuthorityShow: false,
      modeSwitch: 1,
      SimulationTestShow: false,
      communication: null,
      shiftConfigShow: false,
      registerShow: false,
      appletRegisterShow: false,
      CuttingMethod: 1,  //1:自动 2:手动
      upwd: "",
      manualLockUpwd: "",
      manualLockUpwd_1: "",
      imgs: [a1, a2, a3, a4],
      itemNo: "",
      focus: 0, // 0:输入框(),1:A称,2:B称,3:C称,4:D称,5:称量参数配置 6:ERP数据对接配置 -1:隐藏焦点 7:设置中心 8:水位校正 9:手动下料
      tempFocus: 5,
      height:
        document.documentElement.clientHeight || document.body.clientHeight,
      WeighingStateList: [0, 0, 0, 0], //加料状态  -1:不存在料单 0:料单读取完毕 1:准备完毕  2:桶满 3:换桶完毕 4:加料异常 5:加料中 6:称量完成 7:修改料单完成
      formData: {
        itemNo: "", //料单号
        clothWeight: "", //布重
        color: "", //颜色
        unit: "KG",
        list: [], //料单列表  称量状态 不同的称量状态使用不同的背景色  0:未称量 1:称量中 2:已称量
        realTime: [0, 0, 0, 0], //称实时数据
        WeighingInformation: [{}, {}, {}, {}], //当前执行中的料单信息
      },
      time: null,
      failItemNo: "",
      config: {
        allowableError: "", //误差量
        capacity: "", //桶容量
        reduce: "", //减速位
        StopTime: "", //稳定时间
        compensate: "", //补偿量
        compensationCoefficient: "", //补偿系数
        fns: [], //串口号设置列表
        materials: [], //管道号设置列表
        ways: [],
        //数据对接
        way: 1, //方式号
        user: "", //用户
        upwd: "", //密码
        ip: "", //IP地址
        port: "", //端口号
        tableName: "", //表名称
        connect1: "", //登录/单头
        connect2: "", //获取物料信息/单头加物料
        connect3: "", //修改物料
        maxWaterLevel: 0,//最大水位
        alarmWaterLevel1: 0,//低水位
        alarmWaterLevel2: 0,//高水位
        WaterLevelNuit: "",
      },
      setIntervalTime: null,
      dayTime: new Date().toLocaleString(),
      list: [],
      listAll: [[], [], [], []],
      dataStart: true,
      COMStart: [0, 0, 0, 0],
      portResult: [0, 0, 0, 0],
      itemMadolShow: [false, false, false, false, false],
      dyeingClassList: [],
      dyeingClassShow: false,
      RequltData: {
        itemNo: "",
        dyeingClass: "",
      },
      SelectTime: {
        startTime: new Date(),
        endTime: new Date(),
      },
      recordData: [],
      requestRecord: {
        itemNo: "",
        dateTime: "",
        isNotShow: true,
      },
      CompletionPrompt: false,
      selectRow: {},
      selectRowIndex: -1,
      shiftIdentification: [
        {
          code: "1",
          name: "早",
        },
        {
          code: "2",
          name: "中",
        },
        {
          code: "3",
          name: "晚",
        },
      ],
      shiftConfigTime: [
        "00:00",
        "01:00",
        "02:00",
        "03:00",
        "04:00",
        "05:00",
        "06:00",
        "07:00",
        "08:00",
        "09:00",
        "10:00",
        "11:00",
        "12:00",
        "13:00",
        "14:00",
        "15:00",
        "16:00",
        "17:00",
        "18:00",
        "19:00",
        "20:00",
        "21:00",
        "22:00",
        "23:00",
      ],
      shiftConfig: {
        code: "1", //标识
        codeName: "", //名称
        startTime: "", //开始时间段
        endTime: "", //结束时间段
      },
      shiftsConfig: [],
      registerConfig: {
        corporateName: "", //公司名称
        corporateAddress: "", //公司地址
        telePhoneList: [], //允许查看远程数据电话列表
        mac: "", //设备唯一编码
        upwd: "", //密码(查看,修改)
        Token: "", //空,注册成功后生成
        invitationCode: "", //空,注册成功后生成
      },
      registerConfigTelephone: "",
      corpratePhoneList: [],
      waterLevelList: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},],//水位百分比数据
      checkValue: ""
    };
  },
  watch: {
    itemNo(n, o) {
      if (this.focus == 0)
        if (this.time) {
          clearTimeout(this.time);
          this.time = null;
        }
      this.time = setTimeout(() => {
        let tempItemNo=n.split(" ").join("")
        if(tempItemNo!=n){
          this.itemNo=tempItemNo
        }else{
          let req = { itemNo: n, dyeingClass: "" };
          this.requestRecord.itemNo = n;
          this.requestRecord.dateTime = new Date().getTime();
          this.requestRecord.isNotShow = true;
          let res = __communication.submitItemNo(JSON.stringify(req));
          if (res.code == -2) {
            this.failItemNo = n;
          } else {
            this.failItemNo = "";
          }
        }
      }, 500);
    },
    focus(n, o) {
      this.blur();
    },
    modelShow(n, o) {
      if (n) {
        this.upwd = "";
      }
    },
    appletRegisterShow(n, o) {
      if (n) {
        this.getCorporatePhone()
      }
    },
    modeSwitch(n, o) {
      setTimeout(() => {
        __communication.setFocus()
        let req = { itemNo: "", dyeingClass: "" };
        __communication.submitItemNo(JSON.stringify(req));
      }, 300)
      if (o == 2, n == 1) {
        this.manualLockUpwd_1 = ""
      }
    }
  },
  methods: {

    initFocus() {
      this.focus = 0;
    },
    cellClick({ row, rowIndex }) {
      if (
        this.formData.WeighingInformation[row.address].feedingStatus < 2 &&
        this.formData.WeighingInformation[row.address].total < 2 &&
        row.itemWeighingState == 0
      ) {
        __communication.cellClick(
          JSON.stringify({ id: row.id, address: row.address })
        );
      }
    },
    setSimulationTestShow() {
      this.SimulationTestShow = true;
      this.focus = 0;
      __communication.openSimulationTest();
    },
    inputDblclick() {
      __communication.openKeyboard();
    },
    //弹窗 完成/异常/换桶
    //关闭
    close() {
      this.$XModal.confirm("您确定要关闭吗？").then((res) => {
        if (res == "confirm") {
          clearInterval(this.setIntervalTime);
          __communication.close();
        }
      });
    },
    //失去焦点 焦点处理
    blur() {
      if (this.modelShow || this.dyeingClassShow || this.WeighingAuthorityShow) {
        return
      }
      try {
        switch (this.focus) {
          case -1:
            this.$refs.itemNo_off.focus();
            break;
          case 0:
            this.$refs.itemNo.focus();
            break;
          case 9:
            this.$refs.manual.setFocus()
            break;
          default:
            break;
        }
      } catch (error) {
        console.log("获取焦点失败");
      }
    },
    submitItemNoCallback() {
      if (this.failItemNo.length > 0) {
        let req = {
          itemNo: this.itemNo,
          dyeingClass: this.RequltData.dyeingClass,
        };
        __communication.submitItemNo(JSON.stringify(req));
        this.failItemNo = "";
      }
    },
    bodyClick() {
      console.log("点击事件")
      this.blur();
      this.$refs.manual.setFocus();
    },
    setKg(num) {
      let value = num;
      try {
        let config = this.config || {}
        let decimalPlaces = config.decimalPlaces || 0
        let unit = config.viewUnit || 'kg'
        unit = unit.toUpperCase()
        let unitNumbel = 1
        switch (unit) {
          case 'G':
            unitNumbel = 1;
            break;
          case 'KG':
            unitNumbel = 1000;
            break;
          default:
            unitNumbel = 1
            break;
        }
        value = Number(num / unitNumbel).toFixed(decimalPlaces) || 0;
      } catch (error) {
        value = 0;
      }
      if (value == "NaN") {
        value = "";
      }
      return value;
    },
    //获取料单信息
    queryData() {
      if (this.modelShow || this.dyeingClassShow || this.WeighingAuthorityShow) {
        return
      }
      if (this.focus > 4) {
        if (this.focus == 8) {
          this.$refs.waterLevel.init()
        } else if (this.focus == 9) {
          this.$refs.manual.init()
        }
        return;
      }
      try {
        let list_1 = [];
        let list_2 = [];
        let list_3 = [];
        let res = __communication.queryData();
        let resultData = JSON.parse(res.resultData) || { code: -1 };
        let {
          fromData,
          COMStart,
          portResult,
          portPLCResultStart,
          dataStart,
          code,
          modeSwitch,
          WeighingAuthority,
          waterLevelInfoJson = ""
        } = resultData;
        this.WeighingAuthority = WeighingAuthority || "0"
        if (code == -1) {
          return;
        }
        if (modeSwitch == 2) {
          return this.focus = 9
        }
        if (portPLCResultStart < 10 && COMStart[0] < 10) {
          COMStart[0] = 0;
        } else {
          COMStart[0] = 10;
        }
        this.formData = fromData;
        let feedingStatusOff = 0;
        this.formData.WeighingInformation.forEach((item, index) => {
          let feedingStatus = item.feedingStatus || 1;
          if (index > 0) {
            if (feedingStatus < 2) {
              feedingStatusOff++;
            }
            if (
              item.feedingStatus == 2 ||
              item.feedingStatus == 3 ||
              item.feedingStatus == 4 ||
              item.feedingStatus == 8
            ) {
              this.itemMadolShow[index] = true;
            } else {
              this.itemMadolShow[index] = false;
            }
          }
        });
        this.formData.WeighingInformation.forEach((item, index) => {
          if (index > 0) {
            if (!this.itemMadolShow[index] && this.focus == index) {
              this.focus = -1;
            }
          }
        });
        this.COMStart = COMStart;
        this.portResult = portResult;
        this.dataStart = dataStart;
        let list = []
        list = [
          ...this.formData.list.filter((item) => {
            return item.itemWeighingState == 2;
          }),
          ...this.formData.list.filter((item) => {
            return item.itemWeighingState == 1;
          }),
          ...this.formData.list.filter((item) => {
            return item.itemWeighingState == 0;
          }),
        ];
        let checkValue = ""
        list.forEach((item) => {
          let tempCheckValue = item.id + "_" + item.itemWeighingState + "_" + item.itemNo + ";"
          checkValue += tempCheckValue
          if (item.itemWeighingState != 2) {
            if (item.address == 1) {
              list_1.push(item);
            }
            if (item.address == 2) {
              list_2.push(item);
            }
            if (item.address == 3) {
              list_3.push(item);
            }
          }
        });
        if (this.checkValue != checkValue || checkValue == "") {
          let listAll = [[], list_1, list_2, list_3];
          this.listAll = listAll;
          this.list = list;
          this.checkValue = checkValue
        }
        if (feedingStatusOff != 3) {
          if (
            this.focus != 1 ||
            this.focus != 2 ||
            this.focus != 3 ||
            this.focus != 4
          ) {
            this.focus = -1;
            this.formData.WeighingInformation.forEach((item, index) => {
              if (index > 0) {
                if (
                  this.itemMadolShow[index] &&
                  (this.focus != 1 ||
                    this.focus != 2 ||
                    this.focus != 3 ||
                    this.focus != 4)
                ) {
                  this.focus = index;
                }
              }
            });
          }
        } else {
          this.itemMadolShow = [false, false, false, false];
          this.focus = 0;
          if (this.config.dyeingClass) {
            let dyeingClassList = [];
            this.list.forEach((item) => {
              if (
                item.dyeingClass &&
                dyeingClassList.indexOf(item.dyeingClass) == -1
              ) {
                dyeingClassList.push(item.dyeingClass);
              }
            });
            this.dyeingClassList = dyeingClassList;
            if (dyeingClassList.length > 1) {
              this.dyeingClassShow = true;
              this.focus = -2;
              this.$refs.vxeSelect.focus();
            } else {
              this.RequltData.dyeingClass = "";
            }
          }
        }
        let waterLevelList = JSON.parse(waterLevelInfoJson);
        waterLevelList.forEach(item => {
          let PAD = this.config.maxWaterLevel > 0 && item.VAD != 0 ? item.VAD / this.config.maxWaterLevel : 0
          PAD = PAD > 1 ? 1 : PAD
          item.PAD = PAD < 0 ? 0 : PAD * 100
        })
        this.waterLevelList = waterLevelList
      } catch (error) {
        console.log(error);
      }
      if (Array.isArray(this.formData.list) && !this.dyeingClassShow) {
        let completionRecord = 0;
        this.formData.list.forEach((item) => {
          if (item.itemWeighingState == 2) {
            completionRecord++;
          }
        });
        if (
          completionRecord == this.formData.list.length &&
          completionRecord > 0
        ) {
          if (this.requestRecord.isNotShow) {
            this.requestRecord.isNotShow = false;
            this.CompletionPrompt = true;
          }
        } else {
          this.CompletionPrompt = false;
        }
      } else {
        this.CompletionPrompt = false;
      }
      this.manualLockOk()
      this.$forceUpdate();

    },
    dyeingClassSave() {
      this.RequltData.itemNo = this.itemNo;
      __communication.submitItemNo(JSON.stringify(this.RequltData));
      this.focus = 0;
      this.dyeingClassShow = false;
    },
    //设置选中行样式
    rowClassName({ row, rowIndex }) {
      try {
        if (row.itemWeighingState == 2) {
          return "xTableSelectRow_2";
        } else if (row.itemWeighingState == 1) {
          return "xTableSelectRow_1";
        } else if (row.id == this.selectRow.id) {
          return "xTableSelectRow_Id";
        } else {
          return "xTableSelectRow_0";
        }
      } catch (err) {
        console.log("行颜色设置错误", this.selectRow);
      }
    },
    //获取配置信息
    GetConfig() {
      let bool = true;
      this.formData.WeighingInformation.forEach((item) => {
        let feedingStatus = item.feedingStatus || -1;
        if (feedingStatus > 1) {
          bool = false;
        }
      });
      if (bool) {
        this.modelShow = true;
      } else {
        this.$XModal.message({ content: "请结束称料再操作 " });
      }
    },
    madolOk() {
      if (this.upwd == "1951" || this.tempFocus == 7) {
        this.focus = this.tempFocus;
        this.modelShow = false;
        let config = __communication.GetConfig();
        config.dyeingClass = config.dyeingClass ? true : false;
        this.config = config;
      } else {
        this.$XModal.message({ content: "密码错误" });
      }
      if (this.tempFocus == 7) {
        this.quertRceord();
      }
    },
    //提交配置信息
    SetConfig() {
      this.focus = 0;
      setTimeout(() => {
        this.config.materials.forEach((item) => {
          delete item._X_ROW_KEY;
        });
        this.config.fns.forEach((item) => {
          delete item._X_ROW_KEY;
        });
        let config = JSON.parse(JSON.stringify(this.config));
        config.dyeingClass = config.dyeingClass ? 1 : 0;
        __communication.SetConfig(JSON.stringify(config));
      }, 300);
      this.blur();
    },
    //重置称量状态
    ResetData(num) {
      switch (num) {
        case 1:
          this.$XModal.confirm("您确定要重置当前料单吗？").then((res) => {
            if (res == "confirm") {
              let req = { itemNo: "", dyeingClass: "" };
              __communication.submitItemNo(JSON.stringify(req));
              this.itemNo = "";
            }
          });
          break;
        case 2:
          console.log("回车,确定", this.focus);
          __communication.ViewEnter(this.focus + "");
          break;
        default:
          break;
      }
    },
    setWeighingInfo(num) {
      let str = "";
      switch (num) {
        case -1:
          str = "空闲中";
          break;
        case 0:
          str = "请放桶";
          break;
        case 1:
          str = "准备完毕";
          break;
        case 2:
        case 3:
          str = "已满";
          break;
        case 4:
          str = "加料异常";
          break;
        case 5:
          str = "加料中";
          break;
        case 6:
        case 7:
          str = "保存记录";
          break;
        case 8:
          str = "已完成";
          break;
        default:
          break;
      }
      return str;
    },
    setPipe(pipe) {
      let value1 = pipe % 6 || 6;
      let value2 = Math.ceil(pipe / 6);
      if (value2 == 1) {
        return "A" + value1;
      } else if (value2 == 2) {
        return "B" + value1;
      } else if (value2 == 3) {
        return "C" + value1;
      } else if (value2 == 4) {
        return "D" + value1;
      }
    },
    setAddress(address) {
      if (address == 1) {
        return "A称";
      } else if (address == 2) {
        return "B称";
      } else if (address == 3) {
        return "C称";
      } else if (address == 4) {
        return "D称";
      } else if (address == 0) {
        return "PLC";
      }
    },
    ArraySort(params, field, Lift = "ASC") {
      let arr = JSON.parse(JSON.stringify(params));
      var l = arr.length;
      for (let i = 0; i < l; i++) {
        for (let y = i; y > 0; y--) {
          if (field) {
            if (arr[y][field] < arr[y - 1][field]) {
              [arr[y - 1], arr[y]] = [arr[y], arr[y - 1]];
            }
          } else {
            if (arr[y] < arr[y - 1]) {
              [arr[y - 1], arr[y]] = [arr[y], arr[y - 1]];
            }
          }
        }
      }
      return Lift == "ASC" ? arr : arr.reverse();
    },
    setImage(num, callIndex) {
      let config = this.config || {}
      let BarrelWeight = config.barrelWeight || 150
      let value = this.portResult[callIndex];
      let imgs = this.imgs;
      let img = imgs[0];
      switch (num) {
        case -1:
        case 0:
          if (value > BarrelWeight) {
            img = imgs[1];
          } else {
            img = imgs[0];
          }
          break;
        case 1:
          img = imgs[1];
          break;
        case 4:
        case 5:
        case 6:
        case 7:
          img = imgs[2];
          break;
        case 2:
        case 3:
        case 8:
          img = imgs[3];
          break;
        default:
          break;
      }
      return img;
    },
    key__fn(e) {
      if (
        this.focus == 1 ||
        this.focus == 2 ||
        this.focus == 3 ||
        this.focus == 4
      ) {
        let data = [];
        this.itemMadolShow.forEach((item, index) => {
          if (item) {
            data.push(index);
          }
        });
        let focusIndex = data.indexOf(this.focus);
        switch (e.keyCode) {
          case 37: //左
            focusIndex = focusIndex - 1;
            if (focusIndex < 0) {
              focusIndex = 0;
            }
            this.focus = data[focusIndex];
            break;
          case 39: //右
            focusIndex = focusIndex + 1;
            if (focusIndex >= data.length) {
              focusIndex = data.length - 1;
            }
            this.focus = data[focusIndex];
            break;
        }
      }
      switch (e.keyCode) {
        case 13:
          this.CompletionPrompt = false;
          if (this.focus > 0 && this.focus < 5) {
            this.ResetData(2);
            if (
              this.formData.WeighingInformation[this.focus].feedingStatus == 4
            ) {
              let num = 0;
              this.formData.WeighingInformation.forEach((item) => {
                let feedingStatus = item.feedingStatus || 1;
                if (feedingStatus < 2 || item.feedingStatus == 4) {
                  num++;
                }
              });
              if (num == 4) {
                let req = { itemNo: "", dyeingClass: "" };
                __communication.submitItemNo(JSON.stringify(req));
                this.itemNo = "";
              } else {
                this.$XModal.message({
                  content: "请等待其它称,称料完成后再操作",
                });
              }
            }
          }
          if (this.modelShow) {
            this.madolOk();
          } else if (this.dyeingClassShow) {
            setTimeout(() => {
              this.dyeingClassSave();
            }, 500);
          }
          console.log(this.selectRow);
          if (this.selectRow.itemWeighingState == 0 && this.selectRow.id > 0) {
            this.cellClick({ row: this.selectRow, rowIndex: 0 });
          }
          if (this.manualLockShow) {
            this.manualLockOk(1)
          }
          break;
        case 27:
          this.CompletionPrompt = false;
          let num = 0;
          this.formData.WeighingInformation.forEach((item) => {
            let feedingStatus = item.feedingStatus || 1;
            if (feedingStatus < 2 || feedingStatus ==4) {
              num++;
            }
          });
          if (num == 4) {
            let req = { itemNo: "", dyeingClass: "" };
            __communication.submitItemNo(JSON.stringify(req));
            this.itemNo = "";
          }
          this.modelShow = false;
          this.dyeingClassShow = false;
          break;
        default:
          break;
      }
      if (this.focus < 4) {
        switch (e.keyCode) {
          case 38: //上
          case 33: //上
            this.setSelectRow(-1);
            break;
          case 40: //下
          case 34: //下
            this.setSelectRow(1);
            break;
        }
      }
      this.$refs.manual.key__fn(e)
    },
    setSelectRow(i) {
      try {
        let topIndex = 0;
        let index_ = -1;
        let rowIndex_0 = -1;
        console.log(this.selectRow.id);
        this.list.forEach((item, index) => {
          if (item.id == this.selectRow.id && item.itemWeighingState == 0) {
            index_ = index;
          }
          if (rowIndex_0 == -1 && item.itemWeighingState == 0) {
            rowIndex_0 = index;
          }
        });

        let selectRow = {};
        if (index_ > -1) {
          let tempI = index_ + i;
          topIndex = tempI;
          let tempRow = this.list[tempI];
          let row =
            tempRow.itemWeighingState == 0 && tempRow.id > 0
              ? tempRow
              : this.list[index_];
          selectRow = JSON.parse(JSON.stringify(row)) || {};
        } else if (rowIndex_0 > -1) {
          topIndex = rowIndex_0;
          selectRow = JSON.parse(JSON.stringify(this.list[rowIndex_0])) || {};
        }
        this.selectRow = selectRow;
        try {
          if (selectRow.id) {
            this.$refs.detailedTable.scrollToRow(selectRow, "seq");
          }
        } catch (error) { }
      } catch (error) {
        console.log("设置选中行错误");
      }
    },
    quertRceord() {
      let SelectTime = JSON.parse(JSON.stringify(this.SelectTime));
      SelectTime.startTime = new Date(
        new Date(SelectTime.startTime).format("y/m/d", false) + " 00:00:00"
      ).getTime();
      SelectTime.endTime = new Date(
        new Date(SelectTime.endTime).format("y/m/d", false) + " 23:59:59"
      ).getTime();
      let res = __communication.quertRceord(JSON.stringify(SelectTime));
      if (Array.isArray(res)) {
        res.forEach((item) => {
          let saveTime = item.saveTime - 0 || new Date.getTime();
          item.saveTime = new Date(saveTime).format("y-m-d h:i:s");
        });
        this.recordData = res;
      }
    },
    delShift(index) {
      this.shiftsConfig.splice(index, 1);
    },
    //添加班次
    addShift() {
      let b = true;
      this.shiftsConfig.forEach((item) => {
        if (item.code == this.shiftConfig.code) {
          b = false;
        }
      });
      if (b) {
        this.shiftsConfig.push({
          shiftType: this.shiftConfig.code,
          shiftName: this.shiftConfig.codeName,
          shiftCreateTime: this.shiftConfig.startTime,
          shiftEndTime: this.shiftConfig.endTime,
        });
        this.shiftConfig = {
          code: "1",
          codeName: "",
          startTime: "",
          endTime: "",
        };
      } else {
        this.$XModal.message({ content: "标识重复,请重新选中班次标识" });
      }
    },
    //添加可远程查看电话号码
    addTelephone() {
      if (!this.isPhone(this.registerConfigTelephone)) {
        return this.$XModal.message({ content: "请输入正确格式的手机号码" });
      }
      let b = true;
      this.corpratePhoneList.forEach((item) => {
        if (item.corporatePhone == this.registerConfigTelephone) {
          b = false;
        }
      });
      if (b) {
        __communication.updateCorporatePhone(this.registerConfigTelephone)
        this.getCorporatePhone()
      }
    },
    //判断是否是电话号码
    isPhone(str) {
      let reg =
        /^(0|86|17951)?(13[0-9]|15[012356789]|18[0-9]|14[57]|17[678])[0-9]{8}$/;
      if (reg.test(str)) {
        console.log("手机号码格式输入正确");
        return true;
      } else {
        console.log("请输入正确格式的手机号码");
        return false;
      }
    },
    //提交班次信息
    submitShift() {
      setTimeout(() => {
        let shiftsConfig = JSON.parse(JSON.stringify(this.shiftsConfig));
        shiftsConfig.forEach((item) => {
          delete item._X_ROW_KEY;
        });
        __communication.SetShiftConfig(JSON.stringify(shiftsConfig));
        this.shiftConfigShow = false;
      }, 300);
    },
    //提交注册表信息
    submitRegistry() {
      setTimeout(() => {
        new Promise((resolve, reject) => {
          let res = __communication.SetRegistryConfig(
            JSON.stringify(this.registerConfig)
          );
          resolve(res);
        })
          .then((res) => {
            res = res || {};
            this.$XModal.message({ content: res.wxServerRes.message || "" });
          })
          .catch();
        this.registerShow = false;
      }, 300);
    },
    queryConfig() {
      try {
        let config = __communication.GetConfig();
        let registry = __communication.GetConfig("1");
        let shifts = __communication.GetConfig("2");
        config.dyeingClass = config.dyeingClass ? true : false;
        this.config = config;
        this.registerConfig = registry || {};
        this.shiftsConfig = shifts || [];
        this.registerConfig.telePhoneList =
          this.registerConfig.telePhoneList || [];
      } catch (error) { }
    },
    delRow(index) {
      let row = this.corpratePhoneList[index]
      let rowJson = JSON.stringify({ corporateToken: row.corporateToken, corporatePhone: row.corporatePhone })
      __communication.deleteCorporatePhone(rowJson)
      this.getCorporatePhone()
    },
    getCorporatePhone() {
      let res = __communication.getCorporatePhone() || {}
      console.log(res)
      let infoMap = res.wxServerRes.infoMap || {}
      let corpratePhoneList = infoMap.corpratePhoneList || []
      this.corpratePhoneList = corpratePhoneList
    },
    setCommunication() {
      if (window.Formium && window.Formium.external) {
        window.__communication = window.Formium.external.communication;
      } else {
        setTimeout(() => {
          this.setCommunication()
        }, 1000)
      }
    },
    //打开注册弹窗
    setWeighingAuthority() {
      if (this.WeighingAuthority == '1') {
        return this.$XModal.message({ content: "已注册" });
      }
      let config = __communication.GetConfig();
      config.dyeingClass = config.dyeingClass ? true : false;
      this.config = config;
      this.WeighingAuthorityShow = true
    },
    //执行注册
    setWeighingAuthority_() {
      let res = __communication.setWeighingAuthority(this.config.EquipmentIdentificationCode)
      if (res.code != 1) {
        return this.$XModal.message({ content: "注册码错误" });
      } else {
        this.WeighingAuthorityShow = false
      }
    },
    manualLockOk(i = 0) {
      let manualLock = this.config.manualLock || ""
      if (manualLock.length > 0 && this.modeSwitch == 2) {
        if (i) {
          if (this.manualLockUpwd == manualLock) {
            this.manualLockUpwd_1 = this.manualLockUpwd
            this.manualLockUpwd = ""
            this.manualLockShow = false
          } else {
            this.$XModal.message({ content: "密码错误" });
          }
        } else {
          if (this.manualLockUpwd_1 == manualLock) {
            this.manualLockShow = false
          } else {
            this.manualLockShow = true
          }
        }

      } else {
        this.manualLockShow = false
      }

    }
  },
  mounted() {
    this.$nextTick(() => {
      window.submitItemNoCallback = this.submitItemNoCallback;
      document.body.addEventListener("keydown", this.key__fn);
      if (this.setIntervalTime) {
        clearInterval(this.setIntervalTime);
      }
      this.setIntervalTime = setInterval(() => {
        this.queryData();
        this.dayTime = new Date().toLocaleString();
        try {
          if (this.config && this.config.programLock == 1) {
            __communication.setActive()
          }
        } catch (error) {
        }
      }, 1000);
      setTimeout(() => {
        let config = __communication.GetConfig();
        config.dyeingClass = config.dyeingClass ? true : false;
        this.config = config;
        this.blur();
      }, 1000);
    });
    this.setCommunication()
  },
};
</script>

