import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'xe-utils'
import VXETable from 'vxe-table'
import 'vxe-table/lib/style.css'
import './style/index.less'
// 给 vue 实例挂载内部对象，例如：
    Vue.prototype.$XModal = VXETable.modal
    Vue.prototype.$XPrint = VXETable.print
    Vue.prototype.$XSaveFile = VXETable.saveFile
    Vue.prototype.$XReadFile = VXETable.readFile

Vue.use(VXETable)
Vue.config.productionTip = false
window.submitItemNoCallback=function(params) {}
window.addEventListener("hostactivated",function(){
  console.log("hostactivated","窗体已经激活,并未获取焦点")
  window.Formium.external.communication.setFocus()
})
window.addEventListener("load",function(){ 
  console.log("load","页面加载完毕")
})
Date.prototype.format = function (partten = 'y-m-d h:i:s',repair=true) {
  var y = this.getFullYear()
  var m = this.getMonth() + 1
  var d = this.getDate()
  var h = this.getHours() // 获取当前小时数(0-23)
  var i = this.getMinutes() // 获取当前分钟数(0-59)
  var s = this.getSeconds() // 获取当前秒数(0-59)
  var r = partten.replace(/y+/gi, y)
      r = r.replace(/m+/gi, (m < 10 && repair ? '0' : '') + m)
      r = r.replace(/d+/gi, (d < 10 && repair ? '0' : '') + d)
      r = r.replace(/h+/gi, (h < 10 && repair ? '0' : '') + h)
      r = r.replace(/i+/gi, (i < 10 && repair ? '0' : '') + i)
      r = r.replace(/s+/gi, (s < 10 && repair ? '0' : '') + s)
  return r
}
Vue.prototype.$toFixed_1=function(value){
  let tempValue=Number(value)||0 
   return isNaN(tempValue)?0:tempValue.toFixed(1)
}
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
